<template>
    <v-container>
        <v-layout text-xs-left wrap>
            <v-flex xs12 mb-5>
              <!--context-->
              <v-layout justify-center>
                <video width="100%" height="100%" id="player" ref="player" controls></video>
              </v-layout>
              <v-layout class="green lighten-4 pa-2">
                <v-flex xs2>
                  <v-label>ID: {{this.playerVideoInfo.time}}</v-label>
                </v-flex>
                <v-flex xs2 d-flex>
                  <v-icon small color="white">thumb_up</v-icon>
                  <v-label>{{this.playerVideoInfo.praise}}</v-label>
                </v-flex>
              </v-layout>
              <v-layout class="green lighten-4 pa-2 mt-1">
                <v-flex>
                <h4>title: {{this.playerVideoInfo.title}}</h4>
                <h6>tag: {{this.playerVideoInfo.tags}}</h6>
                </v-flex>
                <v-flex xs1>
                  <v-btn id="VMPraise" ref="VMPraise" flat icon color="deep-orange" x-large  @click="addPraise">
                    <v-icon>thumb_up</v-icon>
                  </v-btn>
                </v-flex>
              </v-layout>
                <v-flex xs12 class="mt-5">
                  <!--discuss-->
                  <v-layout class="mt-5">
                    <v-flex xs6>
                      <v-text-field label="NAME" maxLength="32" hide-details append-icon="edit" v-model="inputNickName"/>
                    </v-flex>
                    <v-flex xs6>
                      <v-btn class="ml-5 pt-2" @click="summitChat">summit</v-btn>
                    </v-flex>
                  </v-layout>
                  <v-layout class="mt-3">
                    <v-text-field label="CHAT" maxLength="32" hide-details append-icon="edit" v-model="inputDiscuss"/>
                  </v-layout>
                <v-list two-line class="mt-5">
                  <template v-for="(item, index) in this.discussSet">
                    <v-list-tile :key="index" avatar>
                      <v-list-tile-content>
                        <v-list-tile-title >{{ item.context }} </v-list-tile-title>
                        <v-list-tile-sub-title>{{ item.nickname }} {{ item.time}} {{ item.praise}} </v-list-tile-sub-title>
                      </v-list-tile-content>
                    </v-list-tile>
                    <v-divider :key="item.time"></v-divider>
                  </template>
                </v-list>
                </v-flex>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
import contractABI from '../js/Ethereum'
import ipfsApi from 'ipfs-api'
const myIpfs = ipfsApi('localhost', '5001', {protocol: 'http'})

const fileInfoABI = contractABI.initContract('fileInfo')

export default {
  name: 'Detail',
  data: () => ({
    // 变量
    playerVideoInfo: {},
    inputNickName: '',
    inputDiscuss: '',
    discussSet: []
  }),
  mounted () {
    // 加载时 接收主参数 初始化视频信息
    if (typeof this.$route.query.key !== 'undefined') {
      let key = this.$route.query.key
      this.playerVideoInfo = JSON.parse(localStorage.getItem(key))
      this.player(this.playerVideoInfo.videoHash)
    } else {
    // 如果没有token参数就用系统默认token
      alert('未能获得视频hash值')
    }
  },
  methods: {
    summitChat () {
      if (this.inputDiscuss === '') {
        alert('please input chat content')
        return
      }
      let hashByte = this.playerVideoInfo.videoHash
      let user = this.$store.state.sysAuthor
      let nickNameByte = this.inputNickName
      let chat = this.inputDiscuss

      console.log('summitChat', hashByte, user, nickNameByte, chat)

      contractABI.pushDiscuss(hashByte, user, nickNameByte, chat)
    },
    // getVideoInfo (_videoHash) {
    //   let hashByte = contractABI.hashToByte32(_videoHash)
    //   let that = this
    //   fileInfoABI.getVideoInfoByHash(hashByte)
    //     .then(function (response) {
    //       // let videoHash = contractABI.byte32ToHash(response[0])
    //       // let imgHash = contractABI.byte32ToHash(response[1])
    //       // let author = response[2]
    //       let titleStr = contractABI.byte32ToStr(response[3])
    //       let tagStr = contractABI.byte32ToStr(response[4])
    //       let praise = response[5].toString()
    //       let time = response[6].toString()
    //
    //       that.title = titleStr
    //       that.tags = tagStr
    //       that.praise = praise
    //       that.time = time
    //       console.log('response', response, titleStr, tagStr, praise, time)
    //     })
    //     .catch(function (error) {
    //       console.log('getVideoInfo error', error)
    //     })
    // },
    getVideoDiscuss (_videoHash) {
      let hashByte = contractABI.hashToByte32(_videoHash)
      let that = this
      fileInfoABI.getVideoDiscussLength(hashByte)
        .then(function (response1) {
          // console.log('discussCount', response1, response1[0].toString())

          let discussCount = parseInt(response1[0].toString())

          if (discussCount > 0) {
            for (let i = 0; i < discussCount; i++) {
              fileInfoABI.getVideoDiscuss(hashByte, i)
                .then(function (response2) {
                  let user = response2[0].toString()
                  let nickname = contractABI.byte32ToStr(response2[1])
                  let context = contractABI.byte32ToStr(response2[2])
                  let praise = response2[3].toString()
                  let time = response2[4].toString()

                  console.log('res2', response2, i, response2[1].indexOf('000'), '\nreplace', response2[1].substring(0, response2[1].indexOf('000')), response2[1].replace('000', ''), nickname.length, that.discussSet.length)

                  let discussItem = {
                    'user': user,
                    'nickname': nickname,
                    'context': context,
                    'praise': praise,
                    'time': time
                  }
                  that.discussSet.unshift(discussItem)
                  // console.log('res2', response2, i, user, nickname, context, praise, time, that.discussSet.length)
                })
            }
          }
        })
        .catch(function (error) {
          console.log('getVideoDiscuss error', error)
        })
    },
    addPraise () {
      // console.log('this.praise', this.praise, this.$store.getters.getHomeVideoList('fdfsf'))
      this.playerVideoInfo.praise = parseInt(this.playerVideoInfo.praise) + 1

      // this.$refs.VMPraise.color = 'blue'
    },
    player (videoHash) {
      // video.src = 'http://localhost:8090/ipfs/QmTt4EGNYppgxgqThoSNNXnJRLwCy8SwNnqdfw4DXYrzWe'
      // video.play()

      if (videoHash !== '') {
        let that = this
        const videoControl = this.$refs.player // 需要先设置player控件
        let vHash = videoHash // 'QmQRX5yb1taEavc48ExrYqZQXCZdqVCmHtQDBcswXmdX7A' t2.mp4
        myIpfs.cat(vHash)
          .then(function (response) {
            videoControl.src = response.toString()
            videoControl.play()

            // that.getVideoInfo(vHash)
            that.getVideoDiscuss(vHash)

            // console.log('player hash res', vHash)
          })
          .catch(function (error) {
            console.log('player hash', vHash, 'error', error)
          })
      } else {
        alert('video hash is empty')
      }
    }
  },
  beforeDestroy () {
    // 组件结束前执行
    console.log('log out')
  }
}
</script>

<style scoped>

</style>



