<template>
    <v-container>
        <v-layout text-xs-center wrap>
            <v-flex xs12 mb-5>
                <!--context-->
              <v-layout xs12 class="ma-2">
                <v-flex xs2>
                  <v-btn color="info" onclick="selectVideo.click()" >选择视频文件</v-btn>
                </v-flex>
                <v-flex>
                  <input style="width: 100%; height: 100%;" type="text"  id="selectVideoPath" ref="selectVideoPath" readonly="readonly">
                </v-flex>
              </v-layout>
              <v-layout justify-center  class="green lighten-4 pa-1">
                  <video  ref="player" width="100%" height="100%" controls>
                    <input
                      id="selectVideo"
                      ref="selectVideo"
                      type="file"
                      style="display:none"
                      @change="selectVideo($event)"
                      accept="video/mp4"
                    />
                  </video>
              </v-layout>
              <v-layout class="mt-5">
                <h3>视频封面图片</h3>
              </v-layout>
              <v-flex xs6 height="300">
                <v-img ref="videoImg" onclick="selectVideoImg.click()" class="justify-space-around white lighten-4 body-2" height="300" aspect-ratio="4/3">
                  <input
                    id="selectVideoImg"
                    ref="selectVideoImg"
                    type="file"
                    @change="selectImg($event)"
                    style="display: none"
                    accept="image/jpeg"
                  />
                </v-img>
              </v-flex>
              <v-layout class="mt-3">
                <v-text-field ref="VMVideoTitle" label="TITLE" maxLength="32" hide-details append-icon="edit" v-model="inputTitle"/>
              </v-layout>
              <v-layout class="mt-3">
                <v-text-field label="TAG" maxLength="32" hide-details append-icon="edit" v-model="inputTag"/>
              </v-layout>
              <v-layout>
                <v-label>tag1,tag2</v-label>
              </v-layout>
              <v-layout justify-center class="mt-5">
                <v-btn round color="info" @click="upLoadFile" >uploadFile</v-btn>
                <v-btn round color="info" @click="temp" >temp</v-btn>
              </v-layout>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
import ipfsApi from 'ipfs-api'
import contratABI from '../js/Ethereum'
const myIpfs = ipfsApi('localhost', '5001', {protocol: 'http'})
export default {
  name: 'uploadFile',
  data: () => ({
    // 变量
    inputTitle: '',
    inputTag: ''
  }),
  mounted () {
    // 加载时执行
    contratABI.initContract('fileInfo')
  },
  methods: {
    // 方法合集
    temp () {
      contratABI.initVideoList(7)

      // let reader = new FileReader()
      // reader.readAsDataURL(selectVideoImg)
      // let tostr1 = base64.encode('QmVycWtybcD9JyWiGNJMC5zLb3pGF6QjtkBnJ9i9nfDMSY')
      // let tostr2 = base64.encode('QmUoNt6URzGm6hbe4jKkJ6KugiXD5LzZPsM9gwtYHBe6jV')
      //
      // // let tobyte1 = bs58.decode('QmVycWtybcD9JyWiGNJMC5zLb3pGF6QjtkBnJ9i9nfDMSY').toString('hex').replace(/^1220/, '')
      // let tobyte1 = bs58.decode('QmVycWtybcD9JyWiGNJMC5zLb3pGF6QjtkBnJ9i9nfDMSY').toString('hex').replace(/^1220/, '')
      //
      // if (tobyte1.length !== 64) {
      //   console.log('invalid ipfs format')
      // }
      //
      // tobyte1 = '0x' + tobyte1
      // let buf = Buffer.from(tobyte1.replace(/^0x/, '1220'), 'hex')
      // let tobyte2 = bs58.encode(buf)
      //
      // let uu1 = utils.bufferToHex(utils.setLengthRight('justify-space-around white lighten-4 body-2', 32))
      // let uu2 = utils.setLengthRight(uu1, 32).toString()
      //
      // console.log('vid', tostr1, tostr2, '\nimg', tobyte1, tobyte2, '\nuu', uu1, uu2)
    },
    upLoadFile () {
      let selectVideo = this.$refs.selectVideo
      let videoImg = this.$refs.videoImg
      console.log('select file', videoImg, selectVideo.files[0])
      // selectVideoImg.value 路径；selectVideoImg.files[0].name文件名
      if (typeof videoImg.src === 'undefined') {
        alert('please select video img file')
        return
      }

      if (typeof selectVideo.files[0] === 'undefined') {
        alert('please select video file')
        return
      }

      if (this.inputTitle === '') {
        alert('please input video title')
        return
      }

      if (this.inputTag === '') {
        alert('please input video tag')
        return
      }

      let uploadFile = selectVideo.files[0]

      // 判断浏览器是否支持 FileReader
      if (!window.FileReader) {
        alert('您的设备不支持图片预览功能，如需该功能请升级您的设备！')
        return
      }

      let reader = new FileReader()
      let that = this
      // 这里是最关键的一步，转换就在这里
      if (uploadFile) {
        reader.readAsDataURL(uploadFile)
        reader.onload = function () {
          let buffer = Buffer.from(reader.result)

          if (buffer.length > 1) {
            myIpfs.add(buffer)
              .then(function (response) {
                let videoHash = response[0].hash
                let videoImgBuffer = Buffer.from(videoImg.src)
                console.log('upload video hash', videoHash)
                myIpfs.add(videoImgBuffer)
                  .then(function (response) {
                    let videoImgHash = response[0].hash
                    console.log('upload video img hash', videoImgHash, '\nvideoHash', videoHash, '\nauthor', that.$store.state.sysAuthor, that.inputTitle, that.inputTag)

                    let _category = 'live,koren'
                    let _price = 'type:vip,coin:usd,price:0.35'

                    contratABI.pushVideo(videoHash, videoImgHash, that.$store.state.sysAuthor, _category, that.inputTitle, that.inputTag, _price)
                  })
                  .catch(function (error) {
                    console.log('upload video img error', error)
                  })
              })
              .catch(function (error) {
                console.log('upload video error', error)
              })
          } else {
            alert('not buffer content')
          }
        }
      }
    },
    selectVideo (e) {
      let selectFile = e.target

      // 判断浏览器是否支持 FileReader
      if (!e || !window.FileReader) {
        alert('您的设备不支持图片预览功能，如需该功能请升级您的设备！')
        return
      }

      // 检查文件大小不超10m
      if ((selectFile.files[0].size / 1024) > (1024 * 10)) {
        alert('file max size 10M')
        return
      }
      let reader = new FileReader()
      // 设置文件
      // console.log('selectFile', selectFile, '\nreader', reader, '\npath', selectFile.value, selectFile.files[0].name)
      // 这里是最关键的一步，转换就在这里
      if (selectFile.files[0]) {
        reader.readAsDataURL(selectFile.files[0])

        const player = this.$refs.player
        // const viewImg = this.$refs.videoImg
        // let viewImgData
        const selectVideoPath = this.$refs.selectVideoPath
        this.inputTitle = selectFile.files[0].name.substring(0, selectFile.files[0].name.length - 4).trim()
        let that = this
        // 截取视频第一帧为视频封面
        player.addEventListener('loadeddata', function captureImage () {
          let canvas = document.createElement('canvas')
          canvas.width = player.videoWidth * 0.8
          canvas.height = player.videoHeight * 0.8
          canvas.getContext('2d').drawImage(this, 0, 0, canvas.width, canvas.height)

          // 设置动态添加的元素的样式
          // viewImg.className = 'all'
          // viewImg.crossOrigin = 'anonymous'
          // alert(canvas.toDataURL("image/png"))
          that.$refs.videoImg.src = canvas.toDataURL('image/jpg')
        })

        reader.onload = function () {
          let buffer = Buffer.from(reader.result)
          player.src = buffer
          selectVideoPath.value = selectFile.value
          // VMVideoTitle.value = selectFile[0].name
          // console.log('\nbuffer', buffer, '\nposter', player.poster)
        }
      }
    },
    selectImg (e) {
      let selectFile = e.target

      // 判断浏览器是否支持 FileReader
      if (!e || !window.FileReader) {
        alert('您的设备不支持图片预览功能，如需该功能请升级您的设备！')
        return
      }
      let reader = new FileReader()
      // 设置文件

      // 这里是最关键的一步，转换就在这里
      if (selectFile.files[0]) {
        reader.readAsDataURL(selectFile.files[0])

        // console.log('selectFile', selectFile, '\npath', selectFile.value, selectFile.files[0].name, reader.result)

        let viewImg = this.$refs.videoImg

        reader.onload = function () {
          // let buffer = Buffer.from(reader.result)
          // console.log('\nreader', reader.result)
          viewImg.src = reader.result
        }
      }
    }
  },
  beforeDestroy () {
    // 组件结束前执行
  }
}
</script>

<style scoped>

</style>



