import Vue from 'vue'
import App from './App'

import router from './router'
import store from './js/store'

import Vuetify from 'vuetify'
import './plugins/vuetify'

Vue.use(Vuetify)
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
})



