import Store from './store'
import myAbi from './ABI'
import ipfsApi from 'ipfs-api'

const Web3 = require('web3')
const Eth = require('ethjs-query')
const EthContract = require('ethjs-contract')
const Tx = require('ethereumjs-tx')
const hexBase = require('bs58')
const ethUtil = require('ethereumjs-util')
const ethereumjsAbi = require('ethereumjs-abi')

const myIpfs = ipfsApi('localhost', '5001', {protocol: 'http'})

const web3js = new Web3(new Web3.providers.HttpProvider(Store.state.rinkebyInfura))

// 社区基金负责支付用户动态信息的GAS费用
const CommunityFund = '0x04B3af156A56a497426047Ecf5F4bAE3f534d58F'
const CommunityFundKey = 'f899ed375d1738fac85606987db227c4b91c716abcc3a3baa0115e8648eda5af'

let contractInfo
let ContractAbi

function initContract (contractSymbol) {
  // 检查当前token是否存在
  contractInfo = myAbi.findABIBySymbol(contractSymbol)
  if (typeof contractInfo.abi === 'undefined') {
    alert('not find token' + contractSymbol)
    return
  }

  let contractProvider
  if (contractInfo.host === '1') {
    contractProvider = new Eth(new Web3.providers.HttpProvider(Store.state.mainInfura))
  } else {
    // contractProvider = new Eth(new Web3.providers.HttpProvider(Store.state.rinkebyInfura))
    contractProvider = new Eth(window.web3.currentProvider)
  }

  let ethContract = new EthContract(contractProvider)

  ContractAbi = ethContract(contractInfo.abi).at(contractInfo.address)

  // console.log('contract', ContractAbi, '\ngetABI', contractInfo.functionHashes, 'contractInfo.address', contractInfo.address)
  return ContractAbi
}

