<template>
  <v-container>
    <v-layout
      text-xs-center
      wrap
    >
      <v-flex
        xs12
      >
        <v-layout>
          <v-flex xs12 sm6 offset-sm3>
            <v-card>
              <v-container grid-list-sm fluid>
                <v-layout row wrap>
                  <v-flex
                    v-for="(item,index) in this.$store.state.videoList"
                    :key="index"
                    xs12
                    d-flex
                  >
                    <v-card tile class="d-flex">
                      <!--<v-img-->
                        <!--:src="`https://picsum.photos/500/300?image=${n * 5 + 10}`"-->
                        <!--:lazy-src="`https://picsum.photos/10/6?image=${n * 5 + 10}`"-->
                        <!--aspect-ratio="1"-->
                        <!--class="grey lighten-2"-->
                      <!--/>-->
                      <v-img
                        :src="`${item.imgByte}`"
                        :lazy-src="`${item.imgByte}`"
                        @click="imgPlayer(index,item)"
                        class="grey lighten-2"
                      >
                        <v-container fill-height fluid class="pa-0">
                          <v-layout align-end white--text wrap class=" ma-1">
                            <v-layout class="imgText">
                            <v-flex xs10 text-xs-left lexbox>
                              <v-layout row wrap>
                              <span> {{item.title}}</span>
                              </v-layout>
                              <v-layout row wrap class="mt-1 mb-1" >
                                <span>{{item.tags}}</span>
                              </v-layout>
                            </v-flex>
                            <v-flex xs2 lexbox>
                              <v-layout>
                                <v-icon small color="white" class="halfTrans">thumb_up</v-icon>
                              </v-layout>
                              <v-layout row wrap class="mt-1 mb-1">
                                <span>{{item.time}}</span>
                              </v-layout>
                            </v-flex>
                            </v-layout>
                          </v-layout>
                        </v-container>
                        <!--<v-card-text>{{item.title}} {{item.tags}}</v-card-text>-->

                      </v-img>
                    </v-card>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card>
          </v-flex>
        </v-layout>
        <v-layout justify-center>
          <v-text-field  label="搜索" hide-details append-icon="search" v-model="inputStr"/>
        </v-layout>
        <v-layout justify-center>
          <v-btn round color="info" @click="goUploadFile" >goUploadFile</v-btn>
          <v-btn round color="red" @click="catHash" >catHash</v-btn>
          <v-btn round color="red" @click="getHash" >getHash</v-btn>
          <v-btn round color="blue" @click="player" >playerHash</v-btn>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import contratABI from '../js/Ethereum'
import ipfsApi from 'ipfs-api'
const myIpfs = ipfsApi('localhost', '5001', {protocol: 'http'})

export default {

  data: () => ({
    testHash: 'QmV8BhM3V52q1Z9gHAoHKtJ9PxQdFUvQfxKqeQ1woDvj7d', // toString hello this is a demo by taylor4
    imgHash: 'QmQtBPVBPHDbviq8aNAa4nheQE8H4dPKpDmeHDPrN3pMNe', // dog jpg
    mp4Hash: 'QmZeM34MDAMCcj7k7Tr29NjoKuwF8xdhHQgV9d1uKC3SK7', // FEF801699F97483308017A9E5C2BD5A2.mp4
    selectPath: '',
    inputStr: ''
  }),
  computed: {
    // getImgByte: function (item) {
    //   return this.videoInfo[item].imgByte
    // }
  },
  mounted () {
    contratABI.initContract('fileInfo')

    // 加载视频图片列表
    contratABI.initVideoList(9)
  },
  methods: {
    catHash () {
      if (this.inputStr !== '') {
        let hash = this.inputStr
        let viewControl = this.$refs.img
        myIpfs.cat(hash)
          .then(function (response) {
            viewControl.src = response.toString() // 显示图片hash :QmS14ULjyvsJz6BtoHoNx8X5GApL3bxfiKgGYp16oDBniC
            console.log('cat hash', hash, '\nsus', response.toString())
          })
          .catch(function (error) {
            console.log('cat hash', hash, 'error', error)
          })
      } else {
        alert('inputStr is empty')
      }
    },
    getHash () {
      if (this.inputStr !== '') {
        let hash = this.inputStr
        myIpfs.get(hash)
          .then(function (response) {
            console.log('get hash', hash, '\nsus', response)
          })
          .catch(function (error) {
            console.log('get hash', hash, '\nerror', error)
          })
      } else {
        alert('inputStr is empty')
      }
    },
    player () {
      // video.src = 'http://localhost:8090/ipfs/QmTt4EGNYppgxgqThoSNNXnJRLwCy8SwNnqdfw4DXYrzWe'
      // video.play()

      if (this.inputStr !== '') {
        const videoControl = this.$refs.player
        let vHash = this.inputStr // 'QmQRX5yb1taEavc48ExrYqZQXCZdqVCmHtQDBcswXmdX7A' t2.mp4
        myIpfs.cat(vHash)
          .then(function (response) {
            videoControl.src = response.toString()
            videoControl.play()
            console.log('player hash res', vHash)
          })
          .catch(function (error) {
            console.log('player hash', vHash, 'error', error)
          })
      } else {
        alert('inputStr is empty')
      }
    },
    pushToETH () {
      // const obj = 'zdpuAzE1oAAMpsfdoexcJv6PmL9UhE8nddUYGU32R98tzV5fv'
      let _index = 0
      contratABI.pushVideo(this.videoInfo[_index].videoHash, this.videoInfo[_index].imgHash, this.videoInfo[_index].author, this.videoInfo[_index].title, this.videoInfo[_index].tags)
    },
    imgPlayer (index, item) {
      // this.$router.push({path: '/Detail', query: {index: index, time: time}})
      // this.$store.commit('setVideoPlayerParam', item)
      let videoKey = index + item.time
      localStorage.setItem(videoKey, JSON.stringify(item))
      console.log('go player hash: ', index, videoKey)
      let routeData = this.$router.resolve({
        path: '/Detail',
        query: {
          key: videoKey
        }
      })
      window.open(routeData.href, '_blank') // 新窗口打开
    },
    goUploadFile () {
      console.log('go uploadFile: ')
      // this.$router.push({path: '/Detail', query: {hash: videoHash}})
      let routeData = this.$router.resolve({
        path: '/uploadFile'
      })
      window.open(routeData.href, '_blank') // 新窗口打开
    }
  }
}
</script>

<style>
.halfTrans {
  background-color: rgba(0,0,0,0.3);
  border-radius: 1rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  padding-bottom: 0.1rem;
}

.imgText {
  background: linear-gradient(to bottom, transparent, #000000);
}
</style>



