
/** 合约集合 */
/** symbol区分不同合约标识 host 1=main, 4=rinkeby */
const ABIs = [
  {
    symbol: 'fileInfo',
    host: '4', // main
    address: '0x5a4af5e114f48009063cea3b802e944c5d1fd809',
    functionHashes: [{
      'a286c0b9': 'getVideoCount()',
      'bc9d425c': 'getVideoDiscuss(bytes32,uint256)',
      'be532b3e': 'getVideoDiscussLength(bytes32)',
      'efd623aa': 'getVideoInfoByHash(bytes32)',
      'c43563c8': 'getVideoInfoByIndex1(uint256)',
      '01fb0f09': 'getVideoInfoByIndex2(uint256)',
      '9406f61e': 'pushDiscuss(bytes32,address,bytes32,bytes32)',
      '1c0ee667': 'pushVideo(bytes32,bytes32,address,bytes32,bytes32,bytes32,bytes32)',
      'bfa9e78e': 'pushVideoDiscussPraise(bytes32,uint256)',
      '73fda8cf': 'pushVideoPraise(bytes32)',
      '3ee8019a': 'setVideoPrice(bytes32,bytes32)'
    }],
    abi: [{'constant': true, 'inputs': [{'name': '_index', 'type': 'uint256'}], 'name': 'getVideoInfoByIndex2', 'outputs': [{'name': '', 'type': 'bytes32'}, {'name': '', 'type': 'uint256'}, {'name': '', 'type': 'bytes32'}, {'name': '', 'type': 'uint32'}], 'payable': false, 'stateMutability': 'view', 'type': 'function'}, {'constant': false, 'inputs': [{'name': '_videoHash', 'type': 'bytes32'}, {'name': '_imgHash', 'type': 'bytes32'}, {'name': '_author', 'type': 'address'}, {'name': '_category', 'type': 'bytes32'}, {'name': '_title', 'type': 'bytes32'}, {'name': '_price', 'type': 'bytes32'}, {'name': '_tags', 'type': 'bytes32'}], 'name': 'pushVideo', 'outputs': [], 'payable': false, 'stateMutability': 'nonpayable', 'type': 'function'}, {'constant': false, 'inputs': [{'name': '_videoHash', 'type': 'bytes32'}, {'name': '_price', 'type': 'bytes32'}], 'name': 'setVideoPrice', 'outputs': [], 'payable': true, 'stateMutability': 'payable', 'type': 'function'}, {'constant': false, 'inputs': [{'name': '_videoHash', 'type': 'bytes32'}], 'name': 'pushVideoPraise', 'outputs': [], 'payable': false, 'stateMutability': 'nonpayable', 'type': 'function'}, {'constant': false, 'inputs': [{'name': '_videoHash', 'type': 'bytes32'}, {'name': '_user', 'type': 'address'}, {'name': '_nickname', 'type': 'bytes32'}, {'name': '_context', 'type': 'bytes32'}], 'name': 'pushDiscuss', 'outputs': [], 'payable': false, 'stateMutability': 'nonpayable', 'type': 'function'}, {'constant': true, 'inputs': [], 'name': 'getVideoCount', 'outputs': [{'name': '', 'type': 'uint256'}], 'payable': false, 'stateMutability': 'view', 'type': 'function'}, {'constant': true, 'inputs': [{'name': '_videoHash', 'type': 'bytes32'}, {'name': '_discussID', 'type': 'uint256'}], 'name': 'getVideoDiscuss', 'outputs': [{'name': '', 'type': 'address'}, {'name': '', 'type': 'bytes32'}, {'name': '', 'type': 'bytes32'}, {'name': '', 'type': 'uint32'}, {'name': '', 'type': 'uint256'}], 'payable': false, 'stateMutability': 'view', 'type': 'function'}, {'constant': true, 'inputs': [{'name': '_videoHash', 'type': 'bytes32'}], 'name': 'getVideoDiscussLength', 'outputs': [{'name': '', 'type': 'uint256'}], 'payable': false, 'stateMutability': 'view', 'type': 'function'}, {'constant': false, 'inputs': [{'name': '_videoHash', 'type': 'bytes32'}, {'name': '_discussID', 'type': 'uint256'}], 'name': 'pushVideoDiscussPraise', 'outputs': [], 'payable': false, 'stateMutability': 'nonpayable', 'type': 'function'}, {'constant': true, 'inputs': [{'name': '_index', 'type': 'uint256'}], 'name': 'getVideoInfoByIndex1', 'outputs': [{'name': '', 'type': 'bytes32'}, {'name': '', 'type': 'bytes32'}, {'name': '', 'type': 'address'}, {'name': '', 'type': 'bytes32'}, {'name': '', 'type': 'bytes32'}, {'name': '', 'type': 'bytes32'}], 'payable': false, 'stateMutability': 'view', 'type': 'function'}, {'constant': true, 'inputs': [{'name': '_videoHash', 'type': 'bytes32'}], 'name': 'getVideoInfoByHash', 'outputs': [{'name': '', 'type': 'bytes32'}, {'name': '', 'type': 'bytes32'}, {'name': '', 'type': 'bytes32'}, {'name': '', 'type': 'bytes32'}, {'name': '', 'type': 'bytes32'}, {'name': '', 'type': 'uint32'}, {'name': '', 'type': 'uint256'}], 'payable': false, 'stateMutability': 'view', 'type': 'function'}]
  }
]

function findABIBySymbol (_symbol) {
  for (var i = 0; i < ABIs.length; i++) {
    if (ABIs[i].symbol === _symbol) {
      return {
        symbol: ABIs[i].symbol,
        host: ABIs[i].host,
        address: ABIs[i].address,
        functionHashes: ABIs[i].functionHashes,
        abi: ABIs[i].abi
      }
    }
  }
  return ''
}

export default {findABIBySymbol, ABIs}



